@import url(https://fonts.googleapis.com/css?family=Comic+Neue|Noto+Sans+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    box-sizing: border-box;
}
*:before, *:after{
    box-sizing: inherit;
}
/*
font-family: 'Comic Neue', cursive;
font-family: 'Noto Sans JP', sans-serif;*/
body {
    margin: 0;
    padding: 0;
    font: 16px/1.5 'Noto Sans JP', sans-serif;
    background-color: rgb(194, 184, 201);
}

h1,h2,h3,h4,h5,h6 {
    font-family: 'Comic Neue', cursive;
}

a {
    font-size: 20px;
    font-weight: bold;
}

img {
    max-width: 200px;
    height: auto;
    margin: 0 auto;
}


p.loading {
  text-align: center;
  font-weight: bold;
  font-size: 2.5em;
  color: crimson;
}




div.blog-articles {}
div.blog-articles h2 {
    padding: 0 0 0 5em;
    font-size: 2.5rem;
}
div.blog-articles.blog ul {
    display: flex;
    justify-content: space-evenly;

}
div.blog-articles.blog li{
    list-style: none outside;
    border: 3px solid palevioletred;
    border-radius: 0.5;
    padding: 0.5rem;
}

div.blog-articles p{

}

div.blog-articles a {
    text-decoration: none;
    padding: 0.5rem;
    color: black;
    font-size: 15px;
}








th {
    width: 100% !important;
}


@media screen and (max-width: 800px){
    nav.navbar-menu a {
        width: 100%;       
        padding: 10px 5px;
    }

    div.welcome-wrapper > p a{
        width: 100%;
    }
}

