nav.navbar-menu {    
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    background-color:rgb(116, 56, 112);
    
}

nav.navbar-menu ul {
    display: flex;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

nav.navbar-menu li {
    list-style: none outside;
    display: flex;
    justify-content: space-between;
    padding: 0.8rem;
}


nav.navbar-menu span h1 {
    font-size: 1em;
    padding: 0;
    text-align: center;
    color: wheat;
    background-color: rgb(98, 168, 98);
    border: 3px solid  rgb(80, 136, 80);
    margin-top: 0.3em;
}

nav.navbar-menu span h1:hover {
    
    background-color: rgb(80, 136, 80);
    border: 3px solid  rgb(98, 168, 98);
}

nav.navbar-menu .logo {
    padding: 1rem 2.5rem 0 2.5rem;
    background-color:rgb(116, 56, 112);
}



nav.navbar-menu a {
    text-decoration: none;
    color: wheat;
    padding: 0.5em;
}

nav.navbar-menu a:hover {
    color:  black;
}


footer {
    position: fixed;
    width: 100%;
    bottom: 0;
    text-align: center;
    background-color:rgb(116, 56, 112);
    color: wheat;
    padding: 10px 0;
    font-size: 18px;
    font-weight: bold;
}
.form-wrapper {
    padding: 1em 0 3em 0;
    width: 60%;
    margin: 0 auto;       
    text-align: center;
}

.form-wrapper form{
    width: 60%;
    margin: 0 auto;
}

.form-group{
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
}

.form-group label{    
    padding: 0 1em 1em 0;
    text-align: left;
}

.form-wrapper input,
.form-group textarea {
   padding: 10px;
   outline: none;
   font-size: 20px;
   border-radius: 0.5em;
   border: 1px solid rgb(116, 56, 112);
   background-color: whitesmoke;
}


.form-wrapper [type="submit"] {
    padding: 10px 20px;
    margin: 20px;   
    background-color: rgb(116, 56, 112);
    border-radius: 0.5em;
    color: white;
    border: none;
    font-size: 20px;
    font-weight: bold;
    border: 2px solid rgb(116, 56, 112);
}

.form-wrapper [type="submit"]:hover {
    background-color: white;
    border-radius: 0.5em;
    color: rgb(116, 56, 112);
    border: 2px solid rgb(116, 56, 112);
    font-size: 18px;
    font-weight: bold;
}


.form-wrapper p a {
    background-color: rgb(34, 170, 130);
    padding: 5px;
    border-radius: 0.5em;
    outline: none;
    text-decoration: none;
    color: black;
    margin: 10px;
    border: 2px solid rgb(34, 170, 130);
}

form p > span {
    color: rgb(228, 32, 32);
    text-decoration: underline;
}
h2.sweets-page-h2, h4 {
    text-align: center;
}

h4 {
    color: rgb(170, 26, 45);
}

div.cakes{
    display: flex;
    flex-wrap: wrap;
    width: 85%;
    margin: 0 auto;
    margin-bottom: 5em;
    justify-content: space-around;
}

div.cake-wrapper{
    flex-basis: 28%;
    padding: 2em 1em;
    margin: 1.5em;
    display: flex;
    flex-direction: column;
    text-align: center;
    border: 2px solid rgb(116, 56, 112);
    box-shadow: inset 0px 0px 20px 5px rgba(0,0,0,0.5);
}

div.cake-wrapper h2{
    margin-top: 0;
}

div.mini-wrapper{
    display: flex;
    padding: 0 0.5em;
}

div.cake-wrapper div.text-wrapper{
    flex: auto;
    margin-top: 1em;
}

div.cake-wrapper p.cake-description{
    margin-left: 0.8em;
}
div.cake-wrapper div a:first-child {
    margin-right: 0.3em;
}
div.cake-wrapper div a:last-child {
    margin-left: 0.3em;
}

a[type="submit"] {
    background-color: rgb(116, 56, 112);
    color: white;
    padding: 0.5rem;
    font-size: 15px;
    border: 2px solid rgb(116, 56, 112);
    border-radius: 0.5em;
}
a[type="submit"]:hover {
    background-color: rgb(116, 56, 112);
    color: white;
    border: 2px solid white;
    font-size: 15px;
    font-weight: bold;
}

li {
    list-style: none outside;
    color: navy;
}

div.cake-wrapper div button {
    font-size: 15px;
    padding: 0.5em;
    margin: 0.5em;

}

p > span {
    color: tomato;
}

.cake-price {
    color: rgb(179, 0, 0);
}


div.welcome-user-message {
    width: 40%;
    margin: 0.5rem auto;
    padding: 2.5em 0 3em 0;
    text-align: center;
    box-shadow: 0px 5px 10px 5px #a09c9c;
}



div.welcome-user-message h1 {
    margin: 0 auto;
}
div.welcome-user-message h3 {
    padding-bottom: 1.5rem;
}

div.welcome-user-message p {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

div.welcome-user-message a {
    padding: 10px;
    background-color:rgb(116, 56, 112);
    border-radius: 0.5em;
    outline: none;
    text-decoration: none;
    color: wheat;
    margin: 10px;
    border: 2px solid rgb(116, 56, 112);
    
}

div.welcome-user-message a:hover {
    background-color: wheat;
    color:black;
    border: 2px solid wheat;
}

div.welcome-user-message p {
    font-size: 18px;
    margin: 25px
}

div.promotions {
    display: flex;
    justify-content: space-evenly;
    width: 90%;
    margin: 2rem auto;
}

div.promotions h3 {
    text-align: center;
}

div.promotions a {
    text-decoration: none;
    color: #000000;
    align-items: center;
    padding: 1em;
    font-size: 1.5em;
    font-weight: bold;
    background: rgb(167, 108, 130);
}

div.promotions a:hover {
    box-shadow: inset 0px 0px 20px 1px rgba(0,0,0,0.62);
    border: 0px solid #000000;
}
div.contact{
    margin-bottom: 5em;
    display: flex;
    flex-direction: column;
    align-items: center;
}
div.contact h2{
    text-align: center;
}
div.twodiv {
    display: flex;
}
div.address{
    border: 2px solid lightblue;
    padding: 1rem 0 1rem 1rem;
    width: 50%;
    margin-left: 3em;
}
div.address p.addbuilding {
    text-align: center;
    text-decoration: underline;
    font-weight: bold;
    font-size: 1.5em;
}
div.address p.phone {
}
div.address p.phone a{
    text-decoration: none;
    color: rgb(214, 91, 9);
    padding-left: 1em;
}
div.address ul{}
div.address li{
    list-style-type: square;
}
div.address h4{
    
}

div.mycart{
    padding: 1em 0 3em 0;
    width: 80%;
    margin: 0 auto;       
    text-align: center;
}
div.mycart h2{}
div.mycart form{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    
}
div.mycart form .group{
    display: flex;
    justify-content: space-around;
}
div.mycart form .group input {
    text-align: center;
    border-radius: 0.5em;
    outline: none;

}
div.mycart form .group label {
    border-radius: 0.5em;
}

.mycart .total-price {
    align-self: flex-end;
    width: 20%;
    margin: 1em;
   
}
.mycart .total-price label{
    padding-right: 0.5em;
}
.mycart .total-price input{
    padding: 10px;
    width: 60%;
    align-self: flex-end;
    outline: none;
    font-size: 20px;
    border: 1px solid rgb(116, 56, 112);
    background-color: whitesmoke;
    border-radius: 0.5em;
}
.mycart div.buttons {
    display: flex;
    align-items: flex-end;
}
 
.mycart [type="submit"],
.after-order {
    width: 20%;
    align-self: flex-end;
    padding: 10px 20px;
    margin: 20px;   
    background-color: rgb(116, 56, 112);
    color: white;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid rgb(116, 56, 112);
    border-radius: 0.5em;
    text-decoration: none;
}
 
.mycart [type="submit"]:hover,
.after-order:hover {
    background-color: white;
    border-radius: 0.5em;
    color: rgb(116, 56, 112);
 
}


.cart-empty {
  color:  rgb(23, 59, 49);
  font-size: 2em;
  margin: 3em;

}
.order-wrapper {
    padding: 1em 0 3em 0;
    width: 60%;
    margin: 0 auto;       
    text-align: center;
}

.order-wrapper form{
    width: 60%;
    margin: 1em auto;
}

.order-group{
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
}

.order-group label{    
    padding: 0 1em 1em 0;
    text-align: left;
}

.order-wrapper input,
.order-wrapper select {
   padding: 10px;
   outline: none;
   font-size: 20px;
   border-radius: 0.5em;
   border: 1px solid rgb(116, 56, 112);
   background-color: whitesmoke;
}


.order-wrapper [type="submit"],
.order-wrapper .after-order {
    padding: 10px 20px;
    margin: 20px;   
    background-color: rgb(116, 56, 112);
    border-radius: 0.5em;
    color: white;
    border: none;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid rgb(116, 56, 112);
    text-decoration: none;
}

.order-wrapper [type="submit"]:hover ,
.order-wrapper .after-order:hover {
    background-color: white;
    border-radius: 0.5em;
    color: rgb(116, 56, 112);
    border: 2px solid rgb(116, 56, 112);
    font-size: 18px;
    font-weight: bold;
    text-decoration: none;

}

.myorders{
    margin: 0 auto;
    padding: 2em 0;
    width: 90%;
    display: flex;
    flex-direction: column;
}
.myorders h2{
    text-align: center;
}
.myorders .orderdetails{
    border: 2px solid rgb(23, 59, 49);
    padding: 0.5em;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 1em;
}

.myorders .orderdetails div.info{
    flex-direction: row;
    display: flex;
    justify-content: space-around;
}

.orderdetails div.info p span {
    color: rgb(185, 46, 21);
}

.orderdetails div.list{}
.orderdetails div.list ul{}
.orderdetails ul li{
    list-style: none outside;
}
.orderdetails div.list ul p{
    padding-top: 2em;
}
.orderdetails div.list ul button,
.orderdetails div.list button{
    padding: 10px 20px;
    border-radius: 0.5em;
    background-color:rgb(49, 128, 105);
    color: whitesmoke;
    border: none;
    font-size: 18px;

}
     
.orderdetails div.list > button {
    margin-left: 40px;
}

.orderdetails div.list ul button:hover,
.orderdetails div.list button:hover {
    background-color:whitesmoke;
    color: rgb(49, 128, 105);

}


.mycart [type="submit"],
.after-order {
    width: 20%;
    align-self: flex-end;
    padding: 10px 20px;
    margin: 20px;   
    background-color: rgb(116, 56, 112);
    color: white;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid rgb(116, 56, 112);
    border-radius: 0.5em;
    text-decoration: none;
}
 
.mycart [type="submit"]:hover,
.after-order:hover {
    background-color: white;
    border-radius: 0.5em;
    color: rgb(116, 56, 112);
}


.cart-empty {
  color:  rgb(23, 59, 49);
  font-size: 2em;
  margin: 3em;

}
.myorders .important-info {
    margin-top: 0.5em;
    margin-bottom: 2em;
}

.myorders .important-info {
    border: 2px solid rgb(153, 5, 5);
    padding: 0.5em;
}

.myorders .important-info h4{
    text-align: center;
}
.myorders .important-info > p > span {
    text-decoration: underline;
    color: rgb(153, 5, 5);
}
.myorders > h2 > .star {
    size: 1em;
}

.myorders .payment-link {
    text-decoration: none;
    background: #f44336;
    color: white;
    padding: 0.3em;
    border-radius: 0.5em;
}

.myorders .payment-link:hover {
    background:#48be48;
    color: #f44336;
}
